import { DatePipe } from '@angular/common';
import { Component, Input, ViewEncapsulation } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { CoreService } from '../../../core/core.service';
import { CommentDto, CommentUserDto } from '../../../proxies';


@Component({
  selector: 'frigg-job-comment-tile',
  templateUrl: 'job-comment-tile.component.html',
  styleUrls: [ 'job-comment-tile.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class JobCommentTileComponent {
  @Input() comment: CommentDto;

  constructor(private core: CoreService,
              private datePipe: DatePipe) {
  }

  get userText(): Observable<string> {
    return this.core.translation.get('frigg.userCommentedOnDate',
      {
        user: this.getFullName(this.comment.creatorUser),
        date: this.datePipe.transform(this.comment.creationTime, 'short')
      });
  }

  private getFullName(user: CommentUserDto): string {
    return `${user.name} ${user.surname}`;
  }
}




