import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import 'rxjs/add/observable/forkJoin';
import { Observable } from 'rxjs/Observable';
import { CommentDto, EventDto, FriggResponse, JobService, ListResultDto } from '../../../proxies';

@Injectable()
export class JobCommentsResolver implements Resolve<Array<CommentDto>> {

  constructor(private jobService: JobService) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    const jobId: number = +route.params[ 'jobId' ];

    return this
      .jobService
      .getEventsWithComments(jobId)
      .filter((response: FriggResponse<ListResultDto<EventDto>>) => response.success)
      .map(
        (response: FriggResponse<ListResultDto<EventDto>>) => {
          return response.result.items;
        });
  }
}



