import { DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { JobDefinitionResolver } from '../job-definition/detail/resolver/job-definition-resolver.service';
import { SharedModule } from '../shared/shared.module';
import { TaskDetailResolver } from '../task/task-detail/task-detail-resolver.service';
import { JobCommentsRoutingModule, routedComponents } from './job-comments.routing';
import { JobCommentsResolver } from './job-comments/job-comment-resolver/job-comments-resolver.service';
import { JobCommentTileComponent } from './job-comments/job-comment-tile/job-comment-tile.component';
import { JobEventTileComponent } from './job-comments/job-event-tile/job-event-tile.component';

@NgModule({
  imports: [
    SharedModule,
    JobCommentsRoutingModule
  ],
  declarations: [
    ...routedComponents,
    JobCommentTileComponent,
    JobEventTileComponent
  ],
  providers: [
    JobCommentsResolver,
    JobDefinitionResolver,
    TaskDetailResolver,
    DatePipe
  ]
})
export class JobCommentsModule {
}



