import { Component, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoreService } from '../../core/core.service';
import { NotificationMessage } from '../../core/notification/notification-message';
import { CommentDto, CommentUserDto, EventDto, FriggResponse, JobDefinitionDto, JobService, JobTaskDto } from '../../proxies';
import { TileListComponent } from '../../shared/tile-list/tile-list/tile-list.component';
import { JobCommentsService } from './job-comments.service';

interface CommentData {
  EventId: number,
  Comment: string,
  CreatorUserId: number,
  CreationTime: Date
}

@Component({
  selector: 'frigg-job-comments',
  templateUrl: './job-comments.component.html',
  styleUrls: [ './job-comments.component.scss' ],
  providers: [ JobCommentsService ],
  encapsulation: ViewEncapsulation.None
})
export class JobCommentsComponent implements OnInit, OnDestroy {
  events: Array<EventDto> = [];
  job: JobDefinitionDto;
  task: JobTaskDto;

  @ViewChild('eventList') eventList: TileListComponent;

  constructor(private core: CoreService,
              private jobService: JobService,
              private jobCommentsService: JobCommentsService,
              private route: ActivatedRoute) {
  }

  ngOnInit(): void {
    this.route.data.subscribe((data: any) => {
      this.events = data.events;
      this.job = data.job;
      this.task = data.task;
      this.startListeningForNotifications();

      this.core.setTranslatedShellHeader('frigg.commentsFor', {name: this.job.name});

      if (this.task) {
        this.core.shell.setNavigateUrl('/tasks/' + this.job.id + '/' + this.task.id);
        this.core.shell.setNavigateText(this.task.name);
      } else {
        this.core.shell.setNavigateUrl('/jobdefinition/' + this.job.id);
        this.core.shell.setHeader(this.job.name);
      }
    });
  }

  ngOnDestroy(): void {
    this.stopListeningForNotifications()
  }

  startListeningForNotifications(): void {
    this.core.notification
      .listen('private-job-comment-' + this.job.id, 'CommentAdded')
      .subscribe((message: NotificationMessage<CommentData>) => {
        const existingEvent: EventDto = this.events.find((event: EventDto) => event.id === message.data.EventId);
        if (existingEvent) {
          this.addCommentToExistingEvent(message.data, existingEvent);
        } else {
          this.addNewEventWithComments(message.data)
        }
      });
  }

  stopListeningForNotifications(): void {
    this.core.notification.stopListening('private-job-comment-' + this.job.id);
  }

  private addCommentToExistingEvent(commentData: CommentData, existingEvent: EventDto): void {
    const newComment: CommentDto = new CommentDto();
    newComment.comment = commentData.Comment;
    newComment.creatorUserId = commentData.CreatorUserId;
    newComment.creationTime = commentData.CreationTime;
    this.jobCommentsService.getCommentUser(this.events, commentData.CreatorUserId)
      .subscribe((user: CommentUserDto) => {
        newComment.creatorUser = user;
        existingEvent.comments.push(newComment);
      });
  }


  private addNewEventWithComments(commentData: CommentData): void {
    this.jobService.getEvent(this.job.id, commentData.EventId)
      .subscribe((response: FriggResponse<EventDto>) => {
        if (response.success) {
          this.events.push(response.result);
        }
      });
  };


}



