import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import {
  CommentDto,
  CommentUserDto,
  EventDto,
  FriggResponse,
  JobDefinitionDto,
  JobTaskActionDto,
  JobTaskDto,
  UserDto,
  UserService
} from '../../proxies';

@Injectable()
export class JobCommentsService {
  constructor(private userService: UserService) {
  }

  getTaskName(job: JobDefinitionDto, taskId: number): string {
    const task: JobTaskDto = job.tasks.find((t: JobTaskDto) => t.id === taskId);

    return task ? task.name : '';
  }

  getActionName(job: JobDefinitionDto, taskId: number, actionId: number): string {
    const task: JobTaskDto = job.tasks.find((t: JobTaskDto) => t.id === taskId);
    const action: JobTaskActionDto = task ? task.actions.find((a: JobTaskActionDto) => a.id === actionId) : null;

    return action ? action.name : '';
  }

  getCommentUser(events: Array<EventDto>, userId: number): Observable<CommentUserDto> {
    const previousUser = this.findPreviousUser(events, userId);

    if (previousUser) {
      return Observable.of(previousUser);
    }

    return this.userService
      .getUser(userId)
      .map((response: FriggResponse<UserDto>) => {
        if (response.success) {
          const loadedUser = response.result;
          const commentUser = new CommentUserDto();
          commentUser.name = loadedUser.name;
          commentUser.surname = loadedUser.surname;
          commentUser.userName = loadedUser.userName;
          commentUser.imageUrl = loadedUser.imageUrl;
          commentUser.emailAddress = loadedUser.emailAddress;

          return commentUser;
        }
      })
  }

  findPreviousUser(events: Array<EventDto>, userId: number): CommentUserDto | null {
    let previousUser: CommentUserDto = null;

    events.filter((event: EventDto) => {
      const previousComment: CommentDto = event.comments.find((c: CommentDto) =>
        c.creatorUserId === userId);
      if (previousComment && previousComment.creatorUser) {
        previousUser = previousComment.creatorUser;
        return false;
      }
      return true;
    });

    return null;
  };
}



