import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { JobDefinitionResolver } from '../job-definition/detail/resolver/job-definition-resolver.service';
import { TaskDetailResolver } from '../task/task-detail/task-detail-resolver.service';
import { JobCommentsResolver } from './job-comments/job-comment-resolver/job-comments-resolver.service';
import { JobCommentsComponent } from './job-comments/job-comments.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: '0',
    pathMatch: 'full'
  },
  {
    path: ':jobId/:taskId', pathMatch: 'full', component: JobCommentsComponent, data: {title: 'Job comments'},
    resolve: {
      events: JobCommentsResolver,
      job: JobDefinitionResolver,
      task: TaskDetailResolver
    }
  },
  {
    path: ':jobId', pathMatch: 'full', component: JobCommentsComponent, data: {title: 'Job comments'},
    resolve: {
      events: JobCommentsResolver,
      job: JobDefinitionResolver
    }
  }
];

@NgModule({
  imports: [ RouterModule.forChild(routes) ],
  exports: [ RouterModule ],
})
export class JobCommentsRoutingModule {
}

export const routedComponents: Array<any> = [
  JobCommentsComponent
];



