import { Component, Input, ViewEncapsulation } from '@angular/core';
import { CoreService } from '../../../core/core.service';
import { ExecuteActionDialogResult } from '../../../core/dialog/execute-action/execute-action-dialog-result';
import { CommentEventInput, EventDto, JobDefinitionDto, TaskService } from '../../../proxies';
import { JobCommentsService } from '../job-comments.service';

@Component({
  selector: 'frigg-job-event-tile',
  templateUrl: './job-event-tile.component.html',
  styleUrls: [ './job-event-tile.component.scss' ],
  encapsulation: ViewEncapsulation.None
})
export class JobEventTileComponent {
  @Input() job: JobDefinitionDto;
  @Input() event: EventDto;

  constructor(private core: CoreService,
              private taskService: TaskService,
              public jobCommentsService: JobCommentsService) {
  }

  get taskName(): string {
    return this.jobCommentsService.getTaskName(this.job, this.event.taskId);
  }

  get actionName(): string {
    return this.jobCommentsService.getActionName(this.job, this.event.taskId, this.event.actionId);
  }

  onReply(): void {
    const header = this.core.translate('frigg.comment');
    const placeholder = this.core.translate('frigg.comment');

    this.core.dialog.executeAction(
      header,
      placeholder,
      2,
      this.core.translate('frigg.ok')
    ).subscribe((result: ExecuteActionDialogResult) => {
      if (result.ok) {
        this.addComment(result.comment);
      }
    });
  }

  addComment(comment: string): void {
    const input = new CommentEventInput();
    input.jobId = this.job.id;
    input.taskId = this.event.taskId;
    input.eventId = this.event.id;
    input.comment = comment;

    this.taskService.commentEvent(input)
      .subscribe();
  }
}




